import BigNumber from 'bignumber.js'
import erc20 from 'config/abi/erc20.json'
import masterchefABI from 'config/abi/masterchef.json'
import multicall from 'utils/multicall'
import { getMasterChefAddress } from 'utils/addressHelpers'
import farmsConfig from 'config/constants/farms'
import { QuoteToken } from '../../config/constants/types'

const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

const fetchFarms = async () => {
  const data = await Promise.all(
    farmsConfig.map(async (farmConfig) => {
      const lpAdress = farmConfig.lpAddresses[CHAIN_ID]

      const [info, totalAllocPoint, tmdsPerBlock] = await multicall(masterchefABI, [
        {
          address: getMasterChefAddress(),
          name: 'poolInfo',
          params: [farmConfig.pid],
        },
        {
          address: getMasterChefAddress(),
          name: 'totalAllocPoint',
        },
        {
          address: getMasterChefAddress(),
          name: 'tmdsPerBlock',
        },
      ])

      const allocPoint = new BigNumber(info.allocPoint._hex)
      const poolWeight = allocPoint.div(new BigNumber(totalAllocPoint))
      const lpTokenBalanceMC = new BigNumber(info.lpSupply._hex)

      const calls = [
        // Balance of token in the LP contract
        {
          address: farmConfig.tokenAddresses[CHAIN_ID],
          name: 'balanceOf',
          params: [lpAdress],
        },
        // Balance of quote token on LP contract
        {
          address: farmConfig.quoteTokenAdresses[CHAIN_ID],
          name: 'balanceOf',
          params: [lpAdress],
        },
        // Balance of LP tokens in the master chef contract
        // {
        //   address: farmConfig.isTokenOnly ? farmConfig.tokenAddresses[CHAIN_ID] : lpAdress,
        //   name: 'balanceOf',
        //   params: [getMasterChefAddress()],
        // },
        // Total supply of LP tokens
        {
          address: lpAdress,
          name: 'totalSupply',
        },
        // Token decimals
        {
          address: farmConfig.tokenAddresses[CHAIN_ID],
          name: 'decimals',
        },
        // Quote token decimals
        {
          address: farmConfig.quoteTokenAdresses[CHAIN_ID],
          name: 'decimals',
        },
      ]

      const [
        tokenBalanceLP,
        quoteTokenBlanceLP,
        // lpTokenBalanceMC,
        lpTotalSupply,
        tokenDecimals,
        quoteTokenDecimals
      ] = await multicall(erc20, calls)

      let tokenAmount;
      let lpTotalInQuoteToken;
      let tokenPriceVsQuote;
      if (farmConfig.isTokenOnly) {
        tokenAmount = new BigNumber(lpTokenBalanceMC).div(new BigNumber(10).pow(tokenDecimals));
        if (farmConfig.tokenSymbol === QuoteToken.BUSD && farmConfig.quoteTokenSymbol === QuoteToken.BUSD) {
          tokenPriceVsQuote = new BigNumber(1);
        } else {
          tokenPriceVsQuote = new BigNumber(quoteTokenBlanceLP).div(new BigNumber(10).pow(quoteTokenDecimals))
            .div(new BigNumber(tokenBalanceLP).div(new BigNumber(10).pow(tokenDecimals)));
        }
        lpTotalInQuoteToken = tokenAmount.times(tokenPriceVsQuote);
      } else {
        // Ratio in % a LP tokens that are in staking, vs the total number in circulation
        const lpTokenRatio = new BigNumber(lpTokenBalanceMC).div(new BigNumber(lpTotalSupply))

        // Total value in staking in quote token value
        lpTotalInQuoteToken = new BigNumber(quoteTokenBlanceLP)
          .div(new BigNumber(10).pow(quoteTokenDecimals))
          .times(new BigNumber(2))
          .times(lpTokenRatio)

        // Amount of token in the LP that are considered staking (i.e amount of token * lp ratio)
        tokenAmount = new BigNumber(tokenBalanceLP).div(new BigNumber(10).pow(tokenDecimals)).times(lpTokenRatio)
        const quoteTokenAmount = new BigNumber(quoteTokenBlanceLP)
          .div(new BigNumber(10).pow(quoteTokenDecimals))
          .times(lpTokenRatio)

        if (tokenAmount.comparedTo(0) > 0) {
          tokenPriceVsQuote = quoteTokenAmount.div(tokenAmount);
        } else {
          tokenPriceVsQuote = new BigNumber(quoteTokenBlanceLP).div(new BigNumber(10).pow(quoteTokenDecimals))
            .div(new BigNumber(tokenBalanceLP).div(new BigNumber(10).pow(tokenDecimals)));
        }
      }

      const quoteTokenPrice = new BigNumber(1)

      let lpTokenPrice = new BigNumber(0)
      if (farmConfig.isTokenOnly) {
        lpTokenPrice = tokenPriceVsQuote.times(quoteTokenPrice)
      } else {
        lpTokenPrice = new BigNumber(quoteTokenBlanceLP).div(new BigNumber(10).pow(quoteTokenDecimals)).times(new BigNumber(2))
          .times(quoteTokenPrice)
          .div(new BigNumber(lpTotalSupply).div(new BigNumber(10).pow(18)))
      }

      return {
        ...farmConfig,
        tokenAmount: tokenAmount.toJSON(),
        // quoteTokenAmount: quoteTokenAmount,
        lpTotalInQuoteToken: lpTotalInQuoteToken.toJSON(),
        tokenPriceVsQuote: tokenPriceVsQuote.toJSON(),
        quoteTokenPrice: quoteTokenPrice.toJSON(),
        lpTokenPrice: lpTokenPrice.toJSON(),
        poolWeight: poolWeight.toNumber(),
        multiplier: `${allocPoint.div(100).toString()}X`,
        depositFeeBP: info.depositFeeBP,
        harvestInterval: new BigNumber(info.harvestInterval._hex).toNumber(),
        tmdsPerBlock: new BigNumber(tmdsPerBlock).toNumber(),
        tokenDecimals,
        quoteTokenDecimals
      }
    }),
  )
  return data
}

export const fetchFarmsQuoteTokenPrice = (farms) => {
  const bnbBusdPid = 3
  const tmdsBnbPid = 0
  const bnbBusdFarm = farms.find((f) => f.pid === bnbBusdPid)
  const tmdsBnbFarm = farms.find((f) => f.pid === tmdsBnbPid)

  const bnbPrice = new BigNumber(bnbBusdFarm.tokenPriceVsQuote)
  const tmdsPrice = new BigNumber(tmdsBnbFarm.tokenPriceVsQuote).times(bnbPrice)

  return farms.map((farm) => {
    let quoteTokenPrice = new BigNumber(1)
    if (farm.quoteTokenSymbol === QuoteToken.BNB) {
      quoteTokenPrice = bnbPrice
    } else if (farm.quoteTokenSymbol === QuoteToken.TMDS) {
      quoteTokenPrice = tmdsPrice
    }
    const lpTokenPrice = new BigNumber(farm.lpTokenPrice).times(quoteTokenPrice)
    return { ...farm, quoteTokenPrice: quoteTokenPrice.toJSON(), lpTokenPrice: lpTokenPrice.toJSON() }
  })
}

export default fetchFarms
