export const fetchPoolsTotalStatking = async () => {
  const oldPools = poolsConfig.filter((p) => p.sousId < 8)
  const newPools = poolsConfig.filter((p) => p.sousId >= 8)

  const callsOldPools = oldPools.map((poolConfig) => {
    return {
      address: poolConfig.stakingTokenAddress,
      name: 'balanceOf',
      params: [poolConfig.contractAddress[CHAIN_ID]],
    }
  })

  const callsNewPools = newPools.map((poolConfig) => {
    return {
      address: poolConfig.contractAddress[CHAIN_ID],
      name: 'stakedSupply'
    }
  })

  const oldPoolsTotalStaked = await multicall(cakeABI, callsOldPools)
  const newPoolsTotalStaked = await multicall(sousChefABI, callsNewPools)

  return [
    ...oldPools.map((p, index) => ({
      sousId: p.sousId,
      totalStaked: new BigNumber(oldPoolsTotalStaked[index]).toJSON(),
    })),
    ...newPools.map((p, index) => ({
      sousId: p.sousId,
      totalStaked: new BigNumber(newPoolsTotalStaked[index]).toJSON(),
    })),
  ]
}

export const fetchFegPrice = async () => {
  const response = await fetch(`https://api.pancakeswap.info/api/v2/tokens/0xacfc95585d80ab62f67a14c566c1b7a49fe91167`)
  const priceData = await response.json()
  
  return priceData?.data?.price
}

